//1.导入包
import Vue from 'vue'
import Vuex from 'vuex'

//2.挂载
Vue.use(Vuex)

//3.创建vuex实例
let store = new Vuex.Store({
  //状态
  state:{
    num:100
  },
  //变化
  mutations:{
    //里面可以定义很多函数,当触发这个函数就会改变state状态。
    addIncrement(state,stark){
      console.log(stark)
      //接收一个state作为参数，
      state.num += stark.n
    },
    minIncrement(state){
      //接收一个state作为参数，
      state.num -= 5
    }
  }

})

//4.导出store
export default store



