<template>
<div>
    <div class="site-header" style="clear:both;">
        <div class="container">
            <div class="header-logo">
                <router-link to="/" title="小米官网"><img src="/static/img/vn_logo.png" /></router-link>
            </div>
            <div class="header-nav">
                <ul class="nav-list">
                    <li class="nav-category">
                        <a class="btn-category-list" href="catalog.php" style="display:none;">全部商品分类</a>
                    </li>
                    <li class="nav-item">
                        <a class="link" href="category.php?id=76"><span>电子产品</span></a>
                        <div class='item-children'>
                            <div class="container">
                                <ul class="children-list clearfix">
                                    <li class="first">
                                        <div class="figure figure-thumb">
                                            <a href="goods.php?id=27"><img src="" alt="小米电视2 40英寸"></a>
                                        </div>
                                        <div class="title"><a href="goods.php?id=27">小米电视2 40英寸</a></div>
                                        <p class="price">2200<em>元</em>元</p>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
            <!-- <div class="container-user"> -->
            <div class="topbar-cart" id="ECS_CARTINFO">
                <router-link class="cart-mini " to="/cart">
                    <i class="iconfont">&#xe60c;</i> 购物车
                    <span class="mini-cart-num J_cartNum" id="hd_cartnum">(0)</span>
                </router-link>
            </div>
            <div class="topbar-info J_userInfo" id="ECS_MEMBERZONE">
                <span v-text='nickName' v-if="nickName"></span>
                <a  class="link" href="javascript:void(0);" rel="nofollow" @click="loginModalFlag = true" v-if="!nickName">登录</a>
                <span class="sep"  v-if="nickName">|</span>
                <a   class="link" href="javascript:void(0);" rel="nofollow" @click="logout" v-if="nickName">退出</a>
                <a   class="link" href="javascript:void(0);" rel="nofollow"  v-if="!nickName">退出</a>

            </div>
            <!-- </div> -->
        </div>


        <div id="J_navMenu" class="header-nav-menu" style="display: none;">
            <div class="container"></div>
        </div>
    </div>
    <!-- 登录框 -->
    <!-- md-show 控制登录的显示和隐藏 -->
    <div class="md-modal modal-msg md-modal-transition " :class="{'md-show':loginModalFlag}">
    	<div class="md-modal-inner">
    		<div class="md-top">
    			<div class="md-title">login in</div>
    			<button class="md-close">Close</button>
    		</div>
    		<div class="md-content">
    			<div class="confirm-tips">
    				<div class="error-wrap">
    					<span class="error error-show" v-if="errFlag">用户名或密码错误</span>
    				</div>
    				<ul>
    					<li class="regi_form_input">
    						<input type="text" tabindex="1" name="loginname" v-model="userName" @blur="auditingUser"  data-type="loginname" placeholder="请输入用户名"
    						 class="regi_login_input regi_login_input_left">
                {{ usermsg }}
    					</li>
    					<li class="regi_form_input noMargin">
    						<i class="icon IconPwd"></i>
    						<input type="password" tabindex="2" name="password" v-model="userPwd" @blur="auditingPassword" placeholder="请输入密码" class="regi_login_input regi_login_input_left login-input-no input_text"
    						 @keyup.enter="login">
                 {{ pwdmsg }}
    					</li>
    				</ul>

    			</div>
    			<div class="login-wrap">
    				<a href="javascript:;" class="btn-login" @click="login">登录</a>

            <router-link  class="btn-login" to="/register">注册</router-link>
    			</div>
    		</div>
    	</div>
    </div>
   <!-- md-overlay  遮罩层的显示和隐藏 -->
    <div class="md-overlay" v-if="loginModalFlag" @click='loginModalFlag = flase'>
    </div>














</div>
</template>

<script>
  import axios from 'axios'
  export default {
    name:'header',
    data(){
      return{
        userName:'',
        userPwd:'',
        nickName:'',
        loginModalFlag:false,
        usermsg:'',
        pwdmsg:'',
        errFlag:false
      }
    },
    components:{

    },
    //生命周期mounted 挂载完
    mounted:function(){
      this.checkLogin()

    },
    methods:{
      login(){
         axios.post('/api/users/login',{
          userName:this.userName,
          userPwd:this.userPwd
        }).then((result)=>{
          let res = result.data
          this.nickName = res.result.userName
          this.loginModalFlag = false
          this.errFlag = true


        })
      },
      checkLogin(){
        axios.post('/api/users/checkLogin').then((result)=>{
          let res = result.data
          this.nickName = res.result
        })
      },
      logout(){
        axios.post('/api/users/logout').then((result)=>{
          let res = result.data
          this.nickName = ''
        })
      },
      auditingUser:function(){
        if(this.username ==''){
          this.usermsg="用户名不能为空"
        }
      },
      auditingPassword:function(){
        if(this.userPwd ==''){
          this.pwdmsg="密码不能为空"
        }
      }
    }
  }
</script>

<style>

</style>



