<template>
  <div>
    <h2>加减法计算器</h2>
    <div>
      <input type="button" value="-" @click="minHandle()">
      <span>{{ num }}</span>
      <input type="button" value="+" @click="addHandle()">
    </div>
  </div>
</template>

<script>
  export default{
    data(){
      return{

      }
    },
    //实时计算
    computed:{
      num (){
        return this.$store.state.num
      }
    },
    methods:{
      addHandle(){
       // this.num += 5,提交的参数相当于这个参数的key值
       
       //1.第一种
       //this.$store.commit("addIncrement")
       
       //2.第二种
       //this.$store.commit("addIncrement",{name:'stark',age:18,n:5})
       
       //3.第三种
       this.$store.commit({
         type:"addIncrement",
         n:5,
         age:18,
         name:"stark.wang"
       })
      },
      minHandle(){
        this.$store.commit("minIncrement")
      }
    }

  }
</script>

<style>
</style>



